<script>
import $ from 'jquery'
$(document).ready(function () {
  const FBPagePos = $('.webWidth').offset().top
  $(window).scroll(function () {
    const scrollPos = $(window).scrollTop()
    if (scrollPos === FBPagePos || scrollPos > FBPagePos) {
      $('.webWidth').addClass('webFBPageFixed')
    } else if (scrollPos < FBPagePos) {
      $('.webWidth').removeClass('webFBPageFixed')
    }
  })
})
export default {
  data () {
    return {
      candidate: '',
      id: null,
      data: [],
      dataSort: [],
      dataNow: null,
      shareUrl: null,
      shareDesc: '',
      countNow: null, // 現在被點擊貼文的按爛數
      idNow: null, // 現在被點擊的貼文id
      partyForFB: null
    }
  },
  created () {
    const vm = this
    if (this.$route.params.candidate === 'KMT') {
      vm.candidate = '韓國瑜'
      vm.partyForFB = 'KMT'
    } else {
      vm.candidate = '蔡英文'
      vm.partyForFB = 'DPP'
    }
    vm.id = this.$route.params.id
    if (vm.candidate === '韓國瑜') {
      vm.candidateUrl = 'https://spreadsheets.google.com/feeds/cells/1rf8XdYckWqrg0PaCahVtMFQrnHpovs7aalXt5Dbeepc/1/public/values?alt=json'
    } else {
      vm.candidateUrl = 'https://spreadsheets.google.com/feeds/cells/1rf8XdYckWqrg0PaCahVtMFQrnHpovs7aalXt5Dbeepc/2/public/values?alt=json'
    }
    vm.getData()
  },
  methods: {
    getData () {
      const vm = this
      vm.$store.dispatch('isloading', true)
      this.$http.get(vm.candidateUrl).then((response) => {
        vm.data = response.data.feed.entry.filter((item) => {
          return item.gs$cell.row !== '1'
        })
        /* 創造一個用 row 分類的暫時陣列(martix) */
        let matrix = []
        let index = 0
        vm.data.forEach((item) => {
          if (item.gs$cell.row === String(index + 2)) {
            if (matrix[index] === undefined) {
              matrix[index] = []
            }
            matrix[index].push(item)
          } else {
            index = index + 1
            if (matrix[index - 1].length < 8) { // 因為我們 sheet 總共有 7 個資料分類，部分可選填，因此若資料未存在就補空值給他，避免渲染錯誤
              matrix[index - 1].push('')
            }
            if (matrix[index] === undefined) {
              matrix[index] = []
            }
            matrix[index].push(item)
          }
        })
        matrix = matrix.reverse()
        /* 利用暫時陣列創造出擁有 parent 和 children 的正式陣列(matrixData) */
        let matrixData = []
        let children = []
        let dataIndex = 0
        matrix.forEach((item, index) => {
          if (item[1] !== '' && item.length === 8) {
            if (item[1].gs$cell.$t !== '主文') {
              children.push(item)
            } else if (item[1].gs$cell.$t === '主文') { // 之後判斷遇到主文需要將資料推到新的 array 裡
              if (matrixData[dataIndex] === undefined) {
                matrixData[dataIndex] = []
              }
              matrixData[dataIndex].push(item)
              matrixData[dataIndex].push(children.reverse())
              dataIndex = dataIndex + 1
              children = []
            }
          }
        })
        vm.dataSort = matrixData
        vm.pickData()
        // vm.candidateRender()
        vm.initFB()
        vm.getSuckList()
        // console.log(vm.dataSort)
        vm.$store.dispatch('isloading', false)
      })
    },
    initFB () {
      if (window.FB) {
        window.FB.init({
          appId: '486110755337793',
          status: true,
          xfbml: true,
          version: 'v5.0'
        })
        window.FB.XFBML.parse()
      }
    },
    pickData () { // 為了將符合條件的 detail 從 dataSort 裡挑出來
      const vm = this
      vm.dataNow = vm.dataSort.filter((item) => {
        return item[0][0].gs$cell.$t === vm.id
      })
    },
    backToIndex () {
      this.$router.push('/')
    },
    shareShow (id, content) {
      const vm = this
      let party = null
      vm.shareDesc = content
      if (vm.candidate === '韓國瑜') {
        party = 'KMT'
      } else {
        party = 'DPP'
      }
      vm.shareUrl = `${process.env.VUE_APP_TESTURL}/detail/${party}/${id}`
      $('.shareSection').addClass('shareActive')
    },
    closeShare () {
      $('.shareSection').removeClass('shareActive')
    },
    // candidateRender () {
    //   const vm = this
    //   vm.dataNow = vm.dataNow.map((item) => {
    //     if (item[0][1].gs$cell.$t === '主文') {
    //       item[0][1].gs$cell.$t = vm.candidate
    //     }
    //     return item
    //   })
    // },
    scrollAni (item) {
      const targetPos = $(item).offset().top
      $('html, body').animate({ scrollTop: targetPos }, 1000)
    },
    clickSuckBlue (item, count) {
      $(`#blueSuckAniThumb${item}`).removeClass('aniThumbActive')
      $(`#blueSuck${item}`).addClass('text-danger')
      window.setTimeout(() => {
        $(`#blueSuckAniThumb${item}`).addClass('aniThumbActive')
      }, 100)
      this.$store.dispatch('whichSuck', item)
      this.$store.dispatch('detailColor', 'blue')
      this.countSuck(item, count)
    },
    clickSuckGreen (item, count) {
      $(`#greenSuckAniThumb${item}`).removeClass('aniThumbActive')
      $(`#greenSuck${item}`).addClass('text-danger')
      window.setTimeout(() => {
        $(`#greenSuckAniThumb${item}`).addClass('aniThumbActive')
      }, 100)
      this.$store.dispatch('whichSuck', item)
      this.$store.dispatch('detailColor', 'green')
      this.countSuck(item, count)
    },
    countSuck (id, apiCount) { // 統計每則貼文按爛次數
      const vm = this
      if (vm.idNow !== vm.candidate + id) {
        vm.countNow = parseInt(apiCount)
        vm.idNow = vm.candidate + id
      }
      vm.countNow += 1
      let count = vm.countNow
      if (vm.countNow >= 1000) {
        count = Math.floor(count / 1000) + 'K'
      } else {
        count = vm.countNow
      }
      let newCount = document.createTextNode(count)
      if (vm.candidate === '韓國瑜') {
        let renderNum = document.getElementById(`blueCount${id}`)
        renderNum.removeChild(renderNum.childNodes[0])
        renderNum.appendChild(newCount)
        vm.sendSuck(`${id}`, 'KMT')
      } else {
        let renderNum = document.getElementById(`greenCount${id}`)
        renderNum.removeChild(renderNum.childNodes[0])
        renderNum.appendChild(newCount)
        vm.sendSuck(`${id}`, 'DPP')
      }
    },
    sendSuck (item, type) {
      // const api = `https://cors-anywhere.herokuapp.com/us-central1-hitface-260504.cloudfunctions.net/commentCreate?id=${item}&type=${type}` // 測試 api 跨 http 與 https
      const api = `https://us-central1-hitface-260504.cloudfunctions.net/commentCreate?id=${item}&type=${type}` // 實際 api
      this.$http.get(api).then((response) => {
        // console.log(response.data)
      })
    },
    getSuckList () {
      const vm = this
      vm.$store.dispatch('isloading', true)
      // const api = 'https://cors-anywhere.herokuapp.com/us-central1-hitface-260504.cloudfunctions.net/commentList' // 測試 api 跨 http 與 https
      const api = 'https://us-central1-hitface-260504.cloudfunctions.net/commentList' // 實際 api
      this.$http.get(api).then((response) => {
        // console.log(response.data)
        if (response.data.status === 'success') {
          if (vm.candidate === '韓國瑜') {
            const suckList = response.data.comment_list.filter((item) => {
              return item.type === 'KMT'
            })
            // console.log(suckList)
            vm.dataSort.forEach((item) => {
              item[0].push(0)
              suckList.forEach((id) => {
                if (String(id.id) === item[0][0].gs$cell.$t) {
                  item[0].push(id.count)
                }
              })
            })
          } else {
            const suckList = response.data.comment_list.filter((item) => {
              return item.type === 'DPP'
            })
            // console.log(suckList)
            vm.dataSort.forEach((item) => {
              item[0].push(0)
              suckList.forEach((id) => {
                if (String(id.id) === item[0][0].gs$cell.$t) {
                  item[0].push(id.count)
                }
              })
            })
          }
        }
        vm.$store.dispatch('isloading', false)
      })
    },
    gotoIndex () {
      this.$router.push('/')
    }
  }
}
</script>

