<template>
  <div id="app">
    <loading :active.sync="isLoading"></loading>
    <div @click.prevent="gotoTop" class="animated">
      <i class="fas fa-arrow-circle-up fa-2x"></i>
    </div>
    <router-view/>
  </div>
</template>

<script>
import $ from 'jquery'
$(window).scroll(function () {
  let scrollPos = $(window).scrollTop()
  let windowHeight = $(window).height()
  if (scrollPos >= windowHeight / 2) {
    $('.animated').addClass('fadeIn')
  } else {
    $('.animated').removeClass('fadeIn')
  }
})
export default {
  data () {
    return {
    }
  },
  computed: {
    isLoading () {
      return this.$store.state.isLoading
    }
  },
  methods: {
    gotoTop () {
      $('html, body').animate({ scrollTop: 0 }, 1000)
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/all.scss";
@import "~bootstrap/scss/bootstrap";
#app {
  overflow-x: hidden;
}
.fa-arrow-circle-up {
  position: fixed;
  bottom: 1%;
  right: 2.5%;
  cursor: pointer;
  z-index: 999;
  @media(min-width: 769px) {
    right: 2%
  }
}
.animated {
  opacity: 0;
  transition: all .3s;
}
.fadeIn {
  opacity: 1;
}
</style>
