import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading: false,
    suckId: null,
    color: null
  },
  mutations: {
    LOADING (state, status) {
      state.isLoading = status
    },
    SUCK (state, id) {
      state.suckId = id
    },
    COLOR (state, payload) {
      state.color = payload
    }
  },
  actions: {
    isloading (context, status) {
      context.commit('LOADING', status)
    },
    whichSuck (context, id) {
      context.commit('SUCK', id)
    },
    detailColor (context, payload) {
      context.commit('COLOR', payload)
    }
  },
  modules: {
  }
})
